<template>
  <div>
    <van-popup v-model:show="route.showPopover"
               :style="{ height: '100%', width: '100%' }"
               :overlay="false"
               @click="clickOverlay">
      <div class="box-style popover-box"
           :style="props.isTo ? ('margin-top: 335px;') : ('margin-top: 210px;')">
        <div v-for="item in props.coinList"
             :key="item.coin">
          <div class="coin-item"
               @click="clickItem(item)">
            <div><img :src="item.icon"></div>
            <div>{{ item.coin }}</div>
          </div>
          <div class="line"
               v-if="item.coin != 'BNB'"></div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script setup>
import { defineEmits } from 'vue';
import { useRoute } from 'vue-router';

const props = defineProps({
  coinList: Array,
  isTo: Boolean
})
const emit = defineEmits(['selectCoin'])
const route = useRoute()

const clickOverlay = (() => {
  route.showPopover = false
})
const clickItem = ((item) => {
  emit('selectCoin', item)
})

</script>

