<template>
  <div class="box-style fluidity-box">
    <div class="gradient-label-box">
      <label class="gradient-label">{{ $t('home.fluidity') }}</label>
    </div>
    <div class="coin-box fluidity-from">
      <div class="coin-balance-box">
        <div><img src="@/assets/account/sad.png"
               alt=""></div>
        <div>SAD</div>
      </div>
      <div>{{ $t('trade.balance') }}:{{ state.sadBalance.toFixed(4) }}</div>

    </div>
    <div class="input-box">
      <van-field v-model="state.sadInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(true)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllSAD">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
    </div>
    <div class="add">
      <img src="@/assets/trade/add.png"
           alt="">
    </div>
    <div class="coin-box fluidity-to">
      <div class="coin-balance-box">
        <div><img :src="state.coinList[state.selectCoin].icon"></div>
        <div>{{state.coinList[state.selectCoin].coin}}</div>
        <div @click="clickSelectCoin">
          <img src="@/assets/trade/down-arrow.png">
        </div>
      </div>
      <div>{{ $t('trade.balance') }}:{{ state.coinBalance.toFixed(4) }}</div>
    </div>
    <div class="input-box">
      <van-field v-model="state.coinInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(false)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllHYC">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
    </div>
    <div class="my-fluidity-box">
      <div class="my-fluidity-title">{{ $t('trade.myFluidity') }}</div>
      <div class="my-fluidity">{{ state.lpBalance.toFixed(4) }} LP</div>
    </div>
    <div class="button-box function-button-box"
         v-if="!route.isSADTradeAuthorize && 
         ((state.selectCoin == 0 && !route.isHYCTradeAuthorize) || (state.selectCoin == 1 && !route.isUSDTTradeAuthorize))">
      <div class="button-style button-font item-button"
           @click="clickApproveSAD">
        {{ $t('trade.approve') }} SAD
      </div>
      <div class="button-style button-font item-button"
           @click="clickApproveCoin">
        {{ $t('trade.approve') }} {{state.coinList[state.selectCoin].coin}}
      </div>
    </div>
    <div class="button-box"
         v-if="!route.isSADTradeAuthorize && 
         ((state.selectCoin == 0 && route.isHYCTradeAuthorize) || (state.selectCoin == 1 && route.isUSDTTradeAuthorize))">
      <div class="button-style button-font"
           @click="clickApproveSAD">
        {{ $t('trade.approve') }} SAD
      </div>
    </div>
    <div class="button-box"
         v-if="route.isSADTradeAuthorize && 
         ((state.selectCoin == 0 && !route.isHYCTradeAuthorize) || (state.selectCoin == 1 && !route.isUSDTTradeAuthorize))">
      <div class="button-style button-font"
           @click="clickApproveCoin">
        {{ $t('trade.approve') }} {{state.coinList[state.selectCoin].coin}}
      </div>
    </div>
    <div class="button-box"
         v-if="route.isSADTradeAuthorize && ((state.selectCoin == 0 && route.isHYCTradeAuthorize) || (state.selectCoin == 1 && route.isUSDTTradeAuthorize))">
      <div class="button-style button-font"
           :style="(state.sadInputNumber > 0 && state.coinInputNumber > 0) ? ('opacity: 1;') : ('opacity: 0.5;')"
           @click="clickConfirm">
        {{ $t('trade.confirm') }}
      </div>
    </div>
  </div>
  <TradePopover v-if="route.showPopover" :coinList="state.coinList" :isTo="true" @selectCoin="selectCoin" />
</template>

<script setup>

import { reactive, ref, onMounted, onUnmounted, getCurrentInstance, watch } from 'vue';
import { useRoute } from 'vue-router';
import i18n from '@/i18n';
import hyc from '@/json/hyc.json'
import sad from '@/json/sad.json'
import usdt from '@/json/usdt.json'
import token from '@/json/token.json'
import trade from '@/json/trade.json'
import pair from '@/json/pair.json'
import { ethers, utils, constants } from 'ethers';
import { Toast } from 'vant';
import TradePopover from './TradePopover.vue';

const { proxy } = getCurrentInstance()
const route = useRoute()
const timer = ref(0)
const state = reactive({

  hycContract: null,
  usdtContract: null,
  sadContractAddress: process.env.VUE_APP_SAD_CONTRACT_ADDRESS,
  sadContract: null,
  tradeContractAddress: process.env.VUE_APP_TRADE_CONTRACT_ADDRESS,
  tradeContract: null,
  pairContract: null,

  sadBalance: 0,
  coinBalance: 0,
  lpBalance: 0,
  coinList: [
    {
      tag: 0,
      coin: "HYC",
      icon: require('@/assets/account/hyc.png')
    },
    {
      tag: 1,
      coin: "USDT",
      icon: require('@/assets/trade/usdt.png')
    }
  ],
  selectCoin: 0,

  sadInputNumber: 0,
  coinInputNumber: 0,

  rate: 1,
  isInputSAD: true
})

watch(() => route.account, () => {
  getBalance()
  checkSADAuthorizeStatus()
  checkHYCAuthorizeStatus()
  checkUSDTAuthorizeStatus()
})
watch(() => state.sadInputNumber, () => {
  if (state.isInputSAD) {
    state.coinInputNumber = state.sadInputNumber * state.rate
  }
})
watch(() => state.coinInputNumber, () => {
  if (!state.isInputSAD) {
    state.sadInputNumber = state.coinInputNumber / state.rate
  }
})
const inputFocus = ((bool) => {
  state.isInputSAD = bool
})

onMounted(async () => {
  state.sadContract = new ethers.Contract(state.sadContractAddress, sad, proxy.$provider.getSigner())
  state.hycContract = new ethers.Contract(process.env.VUE_APP_HYC_CONTRACT_ADDRESS, hyc, proxy.$provider.getSigner())
  state.usdtContract = new ethers.Contract(process.env.VUE_APP_USDT_CONTRACT_ADDRESS, usdt, proxy.$provider.getSigner())
  state.tradeContract = new ethers.Contract(state.tradeContractAddress, trade, proxy.$provider.getSigner())

  state.isCoinTradeAuthorize = route.isHYCTradeAuthorize

  getBalance()
  getRate()
  checkSADAuthorizeStatus()
  checkHYCAuthorizeStatus()
  checkUSDTAuthorizeStatus()
  setIntervalRefreshData()
})

const setIntervalRefreshData = (() => {
  timer.value = window.setInterval(async () => {
    getBalance()
  }, 3000)
})
const clearIntervalRefreshData = (() => {
  window.clearInterval(timer.value)
})

onUnmounted(() => {
  clearIntervalRefreshData()
})

const getBalance = (async () => {
  const sadBalanceOf = await state.sadContract.balanceOf(route.account)
  state.sadBalance = parseFloat(utils.formatEther(sadBalanceOf))
  getCoinBalance()
})
const getCoinBalance = (async()=> {
  var lpAddress = process.env.VUE_APP_SAD_HYC_CONTRACT_ADDRESS
  if (state.selectCoin == 0) {
    const balanceOf = await state.hycContract.balanceOf(route.account)
    state.coinBalance = parseFloat(utils.formatEther(balanceOf))
  } else if (state.selectCoin == 1) {
    lpAddress = process.env.VUE_APP_SAD_USDT_CONTRACT_ADDRESS

    const balanceOf = await state.usdtContract.balanceOf(route.account)
    state.coinBalance = parseFloat(utils.formatEther(balanceOf))
  }
  const lpContract = new ethers.Contract(lpAddress, token, proxy.$provider.getSigner())
  const lpBalanceOf = await lpContract.balanceOf(route.account)
  state.lpBalance = parseFloat(utils.formatEther(lpBalanceOf))
})
const getRate = (async () => {
  var lpAddress = process.env.VUE_APP_SAD_HYC_CONTRACT_ADDRESS
  if (state.selectCoin == 1) {
    lpAddress = process.env.VUE_APP_SAD_USDT_CONTRACT_ADDRESS
  }
  state.pairContract = new ethers.Contract(lpAddress, pair, proxy.$provider.getSigner())
  const token0 = await state.pairContract.token0()
  const list = await state.pairContract.getReserves()
  if (token0.toLowerCase() == state.sadContractAddress.toLowerCase()) {
    state.rate = parseFloat(utils.formatEther(list[1])) / parseFloat(utils.formatEther(list[0]))
  } else {
    state.rate = parseFloat(utils.formatEther(list[0])) / parseFloat(utils.formatEther(list[1]))
  }
})
const checkSADAuthorizeStatus = (async () => {
  const allowance = await state.sadContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isSADTradeAuthorize = true
  } else {
    route.isSADTradeAuthorize = false
  }
})
const checkHYCAuthorizeStatus = (async () => {
  const allowance = await state.hycContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isHYCTradeAuthorize = true
  } else {
    route.isHYCTradeAuthorize = false
  }
})
const checkUSDTAuthorizeStatus = (async () => {
  const allowance = await state.usdtContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isUSDTTradeAuthorize = true
  } else {
    route.isUSDTTradeAuthorize = false
  }
})

const clickInputAllSAD = (() => {
  state.sadInputNumber = state.sadBalance
  state.coinInputNumber = state.sadInputNumber * state.rate
})
const clickInputAllHYC = (() => {
  state.coinInputNumber = state.coinBalance
  state.sadInputNumber = state.coinInputNumber * state.rate
})
const clickSelectCoin = (()=>{
  route.showPopover = true
})
const selectCoin = ((item) => {
  state.selectCoin = item.tag
  getRate()
})

const clickApproveSAD = (async () => {
  Toast.loading()
  const approve = await state.sadContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isSADTradeAuthorize = true
})
const clickApproveCoin = (()=>{
  if (state.selectCoin == 0) {
    clickApproveHYC()
  } else {
    clickApproveUSDT()
  }
})
const clickApproveHYC = (async () => {
  Toast.loading()
  const approve = await state.hycContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isHYCTradeAuthorize = true
  if (state.selectCoin == 0) {
    state.isCoinTradeAuthorize = route.isHYCTradeAuthorize
  }
})
const clickApproveUSDT = (async () => {
  Toast.loading()
  const approve = await state.usdtContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isUSDTTradeAuthorize = true
  if (state.selectCoin == 1) {
    state.isCoinTradeAuthorize = route.isUSDTTradeAuthorize
  }
})

const clickConfirm = (() => {
  if (parseFloat(state.sadInputNumber) > 0 && parseFloat(state.coinInputNumber) > 0) {
    if (state.sadInputNumber > state.sadBalance || state.coinInputNumber > state.coinBalance) {
      Toast.fail(i18n.global.t('toast.insufficientBalance'))
    } else {
      tradeFluidity()
    }
  } else {
    Toast.fail(i18n.global.t('toast.inputError'))
  }
})

const tradeFluidity = (async () => {
  Toast.loading()
  const nowTime = Math.round(new Date() / 1000) + 100
  const address = (state.selectCoin == 0) ? process.env.VUE_APP_HYC_CONTRACT_ADDRESS : process.env.VUE_APP_USDT_CONTRACT_ADDRESS
  const addLiquidity = await state.tradeContract.addLiquidity(address, state.sadContractAddress,
    utils.parseEther(state.coinInputNumber.toString()), utils.parseEther(state.sadInputNumber.toString()),
    0, 0, route.account, nowTime)
  addLiquidity.wait()
  state.coinInputNumber = 0
  state.sadInputNumber = 0
  getBalance()
  getRate()
})

</script>

<style lang="less" scoped>
@import '@/common/base.less';

.fluidity-box {
  margin: 15px;

  .coin-box {
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
    .scw(@titleColorOne, 14px, 500);
    font-family: PingFangSC-Medium, PingFang SC;
    line-height: 24px;

    .coin-balance-box {
      height: 24px;
      display: flex;
      justify-content: space-between;

      img {
        height: 24px;
        margin-right: 10px;
      }
    }
  }

  .fluidity-from {
    margin-top: 20px;
  }

  .fluidity-to {
    margin-top: 10px;
  }

  .add {
    img {
      height: 22px;
    }
  }

  .my-fluidity-box {
    margin: 10px 15px 0 15px;
    display: flex;
    justify-content: space-between;

    .my-fluidity-title {
      .scw(@titleColorTwo, 14px, 500);
      font-family: PingFangSC-Medium, PingFang SC;
    }

    .my-fluidity {
      .scw(@titleColorOne, 14px, 500);
      font-family: PingFangSC-Medium, PingFang SC;
    }
  }

  .button-box {
    margin: 20px 15px 0 15px;
    padding-bottom: 15px;

    .button-font {
      font-size: 14px;
    }
  }

  .function-button-box {
    display: flex;
    justify-content: space-between;

    .item-button {
      width: calc(calc(100% - 10px) / 2);
    }
  }
}
</style>