<template>
  <div class="box-style exchange-box">
    <div class="gradient-label-box">
      <label class="gradient-label">{{ $t('trade.exchange') }}</label>
    </div>
    <div class="coin-box exchange-from">
      <div class="coin-balance-box">
        <div><img :src="state.isFromSAD ? state.sadIcon : state.coinList[state.selectCoin].icon"></div>
        <div>{{ state.isFromSAD ? "SAD" : state.coinList[state.selectCoin].coin }}</div>
        <div v-if="!state.isFromSAD"
             @click="clickSelectCoin">
          <img src="@/assets/trade/down-arrow.png"
               alt="">
        </div>
      </div>
      <div>
        {{ $t('trade.balance') }}:{{ state.isFromSAD ? state.sadBalance.toFixed(4) :
                state.coinBalance.toFixed(4)
                }}
      </div>
    </div>
    <div class="input-box">
      <van-field v-if="state.isFromSAD"
                 v-model="state.sadInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(true)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllFrom">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
      <van-field v-else
                 v-model="state.coinInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(true)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllFrom">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
    </div>
    <div class="exchange"
         @click="clickExchange">
      <img src="@/assets/trade/exchange.png"
           alt="">
    </div>
    <div class="coin-box exchange-to">
      <div class="coin-balance-box">
        <div>
          <img :src="state.isFromSAD ? state.coinList[state.selectCoin].icon : state.sadIcon">
        </div>
        <div>{{ state.isFromSAD ? state.coinList[state.selectCoin].coin : "SAD" }}</div>
        <div v-if="state.isFromSAD" @click="clickSelectCoin">
          <img src="@/assets/trade/down-arrow.png"
               alt="">
        </div>
      </div>
      <div>{{ $t('trade.balance') }}:{{ state.isFromSAD ?
            state.coinBalance.toFixed(4) :
            state.sadBalance.toFixed(4)
            }}</div>
    </div>
    <div class="input-box">
      <van-field v-if="state.isFromSAD"
                 v-model="state.coinInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(false)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllTo">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
      <van-field v-else
                 v-model="state.sadInputNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')"
                 @focus="inputFocus(false)">
        <template #button>
          <div class="gradient-label"
               @click="clickInputAllTo">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
    </div>
    <div class="button-box">
      <div class="button-style button-font"
           :style="(state.sadInputNumber > 0) ? ('opacity: 1;') : ('opacity: 0.5;')"
           @click="clickConfirm">
        {{ $t('trade.confirm') }}
      </div>
    </div>
  </div>
  <TradePopover v-if="route.showPopover"
                :coinList="state.coinList"
                :isTo="state.isFromSAD"
                @selectCoin="selectCoin" />
</template>

<script setup>

import { reactive, ref, onMounted, onUnmounted, getCurrentInstance, watch } from 'vue';
import { useRoute } from 'vue-router';
import i18n from '@/i18n';
import hyc from '@/json/hyc.json'
import sad from '@/json/sad.json'
import usdt from '@/json/usdt.json'
import trade from '@/json/trade.json'
import { ethers, utils, constants } from 'ethers';
import { Toast } from 'vant';
import TradePopover from './TradePopover.vue';

const { proxy } = getCurrentInstance()
const route = useRoute()
const timer = ref(0)
const state = reactive({
  isFromSAD: true,

  sadContractAddress: process.env.VUE_APP_SAD_CONTRACT_ADDRESS,
  sadContract: null,
  hycContract: null,
  usdtContract: null,
  tradeContractAddress: process.env.VUE_APP_TRADE_CONTRACT_ADDRESS,
  tradeContract: null,

  sadBalance: 0,
  coinBalance: 0,

  coinList: [
    {
      tag: 0,
      coin: "HYC",
      icon: require('@/assets/account/hyc.png')
    },
    {
      tag: 1,
      coin: "USDT",
      icon: require('@/assets/trade/usdt.png')
    }
  ],
  selectCoin: 0,
  sadIcon: require('@/assets/account/sad.png'),

  sadInputNumber: 0,
  coinInputNumber: 0,

  isInputFrom: true
})

watch(() => state.sadInputNumber, () => {
  if ((state.isFromSAD && state.isInputFrom) || (!state.isFromSAD && !state.isInputFrom)) {
    getRate(true)
  }
})
watch(() => state.coinInputNumber, () => {
  if ((state.isFromSAD && !state.isInputFrom) || (!state.isFromSAD && state.isInputFrom)) {
    getRate(false)
  }
})
const inputFocus = ((bool) => {
  state.isInputFrom = bool
})
watch(() => route.account, () => {
  getBalance()
  checkSADAuthorizeStatus()
  checkHYCAuthorizeStatus()
  checkUSDTAuthorizeStatus()
})

onMounted(async () => {
  state.sadContract = new ethers.Contract(state.sadContractAddress, sad, proxy.$provider.getSigner())
  state.hycContract = new ethers.Contract(process.env.VUE_APP_HYC_CONTRACT_ADDRESS, hyc, proxy.$provider.getSigner())
  state.usdtContract = new ethers.Contract(process.env.VUE_APP_USDT_CONTRACT_ADDRESS, usdt, proxy.$provider.getSigner())
  state.tradeContract = new ethers.Contract(state.tradeContractAddress, trade, proxy.$provider.getSigner())

  getBalance()
  checkSADAuthorizeStatus()
  checkHYCAuthorizeStatus()
  checkUSDTAuthorizeStatus()
  setIntervalRefreshData()
})

const setIntervalRefreshData = (() => {
  timer.value = window.setInterval(async () => {
    getBalance()
  }, 3000)
})
const clearIntervalRefreshData = (() => {
  window.clearInterval(timer.value)
})

onUnmounted(() => {
  clearIntervalRefreshData()
})

const getBalance = (async () => {
  const sadBalanceOf = await state.sadContract.balanceOf(route.account)
  state.sadBalance = parseFloat(utils.formatEther(sadBalanceOf))
  getCoinBalance()
})
const getCoinBalance = (async()=>{
  if (state.selectCoin == 0) {
    const balanceOf = await state.hycContract.balanceOf(route.account)
    state.coinBalance = parseFloat(utils.formatEther(balanceOf))
  } else if (state.selectCoin == 1) {
    const balanceOf = await state.usdtContract.balanceOf(route.account)
    state.coinBalance = parseFloat(utils.formatEther(balanceOf))
  }
})

const getRate = (async (bool) => {
  var address = process.env.VUE_APP_HYC_CONTRACT_ADDRESS
  if (state.selectCoin == 1) {
    address = process.env.VUE_APP_USDT_CONTRACT_ADDRESS
  }
  if (bool) {
    if (state.sadInputNumber > 0) {
      const list = await state.tradeContract.getAmountsOut(utils.parseEther(state.sadInputNumber.toString()), [state.sadContractAddress, address])
      state.coinInputNumber = parseFloat(utils.formatEther(list[1]))
    } else {
      state.coinInputNumber = 0
    }
  } else {
    if (state.coinInputNumber > 0) {
      const list = await state.tradeContract.getAmountsOut(utils.parseEther(state.coinInputNumber.toString()), [address, state.sadContractAddress])
      state.sadInputNumber = parseFloat(utils.formatEther(list[1]))
    } else {
      state.sadInputNumber = 0
    }
  }
})

const checkSADAuthorizeStatus = (async () => {
  const allowance = await state.sadContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isSADTradeAuthorize = true
  } else {
    route.isSADTradeAuthorize = false
  }
})
const checkHYCAuthorizeStatus = (async () => {
  const allowance = await state.hycContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isHYCTradeAuthorize = true
  } else {
    route.isHYCTradeAuthorize = false
  }
})
const checkUSDTAuthorizeStatus = (async () => {
  const allowance = await state.usdtContract.allowance(route.account, state.tradeContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isUSDTTradeAuthorize = true
  } else {
    route.isUSDTTradeAuthorize = false
  }
})

const clickSelectCoin = (() => {
  route.showPopover = true
})
const selectCoin = ((item) => {
  state.selectCoin = item.tag
  getCoinBalance()
  if ((state.isFromSAD && state.isInputFrom) || (!state.isFromSAD && !state.isInputFrom)) {
    getRate(true)
  } else if ((state.isFromSAD && !state.isInputFrom) || (!state.isFromSAD && state.isInputFrom)) {
    getRate(false)
  }
})
const clickExchange = (() => {
  state.isFromSAD = state.isFromSAD ? false : true
})

const clickInputAllFrom = (() => {
  if (state.isFromSAD) {
    state.sadInputNumber = state.sadBalance
    state.coinInputNumber = state.sadInputNumber * state.rate
  } else {
    state.coinInputNumber = state.coinList[state.selectCoin].balance
    state.sadInputNumber = state.coinInputNumber * state.rate
  }
})
const clickInputAllTo = (() => {
  if (state.isFromSAD) {
    state.coinInputNumber = state.coinList[state.selectCoin].balance
    state.sadInputNumber = state.coinInputNumber * state.rate
  } else {
    state.sadInputNumber = state.sadBalance
    state.coinInputNumber = state.sadInputNumber * state.rate
  }
})

const clickApproveSAD = (async () => {
  Toast.loading()
  const approve = await state.sadContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isSADTradeAuthorize = true
  tradeExchange()
})
const clickApproveHYC = (async () => {
  Toast.loading()
  const approve = await state.hycContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isHYCTradeAuthorize = true
  tradeExchange()
})
const clickApproveUSDT = (async () => {
  Toast.loading()
  const approve = await state.usdtContract.approve(state.tradeContractAddress, constants.MaxUint256)
  approve.wait()
  route.isUSDTTradeAuthorize = true
  tradeExchange()
})
const clickConfirm = (() => {
  if (parseFloat(state.sadInputNumber) > 0) {
    if ((state.isFromSAD && state.sadInputNumber > state.sadBalance) ||
      (!state.isFromSAD && state.coinInputNumber > state.coinBalance)) {
      Toast.fail(i18n.global.t('toast.insufficientBalance'))
    } else {
      if (state.isFromSAD) {
        if (route.isSADTradeAuthorize) {
          tradeExchange()
        } else {
          clickApproveSAD()
        }
      } else {
        if (state.selectCoin == 0) {
          if (route.isHYCTradeAuthorize) {
            tradeExchange()
          } else {
            clickApproveHYC()
          }
        } else if (state.selectCoin == 1) {
          if (route.isUSDTTradeAuthorize) {
            tradeExchange()
          } else {
            clickApproveUSDT()
          }
        }
      }
    }
  } else {
    Toast.fail(i18n.global.t('toast.inputError'))
  }
})

const tradeExchange = (async () => {
  var address = process.env.VUE_APP_HYC_CONTRACT_ADDRESS
  if (state.selectCoin == 1) {
    address = process.env.VUE_APP_USDT_CONTRACT_ADDRESS
  }
  const inputNumber = state.isFromSAD ? utils.parseEther(state.sadInputNumber.toString()) : utils.parseEther(state.coinInputNumber.toString())
  const addressList = state.isFromSAD ? [state.sadContractAddress, address] : [address, state.sadContractAddress]
  const nowTime = Math.round(new Date() / 1000) + 100
  Toast.loading()
  const exchange = await state.tradeContract.swapExactTokensForTokensSupportingFeeOnTransferTokens(inputNumber, 0, addressList, route.account, nowTime)
  exchange.wait()
  state.sadInputNumber = 0
  state.coinInputNumber = 0
  getBalance()
})

</script>

<style lang="less" scoped>
@import '@/common/base.less';

.exchange-box {
  margin: 15px;

  .coin-box {
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
    .scw(@titleColorOne, 14px, 500);
    font-family: PingFangSC-Medium, PingFang SC;
    line-height: 24px;

    .coin-balance-box {
      height: 24px;
      display: flex;
      justify-content: space-between;

      img {
        height: 24px;
        margin-right: 10px;
      }
    }
  }

  .exchange-from {
    margin-top: 20px;
  }

  .exchange-to {
    margin-top: 10px;
  }

  .exchange {
    img {
      height: 22px;
    }
  }

  .button-box {
    margin: 20px 15px 0 15px;
    padding-bottom: 15px;

    .button-font {
      font-size: 14px;
    }
  }

  .function-button-box {
    display: flex;
    justify-content: space-between;

    .item-button {
      width: calc(calc(100% - 10px) / 2);
    }
  }
}
</style>