<template>
    <div>
        <div class="menu-box">
            <div :class="(state.menuTag == 0) ? state.menuSelectBgStyle : state.menuUnSelectBgStyle">
                <div :class="(state.menuTag == 0) ? state.menuSelectLabelStyle : state.menuUnSelectLabelStyle"
                    @click="clickMenu(0)">
                    {{  $t('trade.exchange')  }}
                </div>
            </div>
            <div :class="(state.menuTag == 1) ? state.menuSelectBgStyle : state.menuUnSelectBgStyle">
                <div :class="(state.menuTag == 1) ? state.menuSelectLabelStyle : state.menuUnSelectLabelStyle"
                    @click="clickMenu(1)">
                    {{  $t('home.fluidity')  }}
                </div>
            </div>
        </div>
        <TradeExchange v-if="state.menuTag == 0" />
        <TradeFluidity v-if="state.menuTag == 1" />
    </div>
</template>

<script setup>

import { reactive } from 'vue';
import TradeExchange from '../components/TradeExchange.vue'
import TradeFluidity from '../components/TradeFluidity.vue'

const state = reactive({
    menuTag: 0,
    menuSelectBgStyle: 'menu-select-bg',
    menuUnSelectBgStyle: 'menu-unselect-bg',
    menuSelectLabelStyle: 'gradient-label',
    menuUnSelectLabelStyle: 'unselect-label'
})

const clickMenu = ((tag) => {
    if (state.menuTag != tag) {
        state.menuTag = tag
    }
})

</script>

